body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.flex {
  display: flex;
  justify-content: space-around;
  margin-top: 75px;
}

.question h1 {
	font-size: 60px;
	margin-top: 75px;
}
.info h1 {
	font-size: 60px;
	margin-top: 75px;
}

.info1 {
	margin-top: 75px;
}

.topnav {
  height: 100px;
  width: 100%;
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  background-color: #C62828;
  overflow-x: hidden;
  padding-top: 15px;
  box-shadow: 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.topnav h1 {
  color: white;
  text-align: center;
  font-size: 50px;
}

.mainPage {
  margin-top: 100px;
  padding: 20px 10px;
  text-align: center;
  margin-left:115px;
  margin-right:115px;
}

.restart {
  position: fixed;
  bottom: 50px;
  right: 200px;
}

.emergency {
  position: fixed;
  bottom: 50px;
  left: 200px;
}
